
const store = [];

function createTask(text) {
  return {
    isDone: false,
    text: text,
  }
}

function toggleTaskState(task) {
  return {
    ...task,
    isDone: !task.isDone,
   
  }
}

function addTaskToStore(task) {
  store.push(task);
}

function updateTaskInStore(task, index) {
   if (store.indexOf(task) != -1) {
     store[index] = toggleTaskState(task);
   }
}

function createTaskNode(task, index) {
  const taskNode = document.createElement("div");
  
  const inputNode = document.createElement("input");
  inputNode.type = "checkbox";
  inputNode.checked = task.isDone;
  inputNode.addEventListener("change", () => {
   updateTaskInStore(task, index);
   renderTasks(store);
  });
  
  const textNode = document.createElement("label");
  textNode.innerHTML = task.text;
  
  taskNode.append(inputNode, textNode);
  
  return taskNode;
}

function renderTasks(store) {
    const sortedStore = sortStore(store);
  
    const todoList = document.querySelector('.todo-list');
    todoList.innerHTML = "";
  
    sortedStore.forEach(function(task, index) {
        todoList.append(createTaskNode(task, index));
    });
  

}


const todoForm = document.querySelector('.todo-form');
todoForm.addEventListener("submit", handleSubmit);

function handleSubmit(event) {
  event.preventDefault();
  const text = event.target.elements[0].value;

  addTaskToStore(createTask(text));
  renderTasks(store);

}

function sortStore(store) {
  return store.sort((a, b) => {
     if (a.isDone < b.isDone) {
        return -1;
      }
      if (a.isDone > b.isDone) {
        return 1;
      }
      return 0;
  });
  
}

renderTasks(store);


  